import md5 from "@/common/SDK/md5.min.js";
export default {
	data() {
		return {
			email: '',
			passwd: '',
			Status: "",
			isShowOauth: true,
			showProvider: {
				weixin: true,
				qq: true,
				sinaweibo: true,
				xiaomi: true
			}
		}
	},
	onShow() {

	},
	onLoad() {
		//APP显示第三方登录



		this.getProvider();

	},
	methods: {
		oauthLogin(provider) {
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes) => {
					console.log("success: " + JSON.stringify(loginRes));
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes) => {
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							uni.setStorage({
								key: 'UserInfo',
								data: {
									username: infoRes.userInfo.nickName,
									face: infoRes.userInfo.avatarUrl,
									signature: '个性签名',
									integral: 0,
									balance: 0,
									envelope: 0
								},
								success: function() {
									uni.hideLoading()
									uni.showToast({
										title: '登录成功',
										icon: "success"
									});
									setTimeout(function() {
										uni.navigateBack();
									}, 300)
								}
							});
						}
					});
				},
				fail: (e) => {
					console.log("fail: " + JSON.stringify(e));
				}
			});
		},
		getProvider() {
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					let len = res.provider.length;
					for (let i = 0; i < len; i++) {
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = true;
					}
					if (res.provider.length == 0) {
						this.isShowOauth = true;
					}
				}
			});
		},
		toPage(page) {
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		doLogin() {
			uni.hideKeyboard();
			//验证邮箱
			if (!(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(this.email))) {
				uni.showToast({
					title: '请填写正确邮箱',
					icon: "none"
				});
				return false;
			}
			// 验证密码
			if (!(/^(\w){6,10}$/.test(this.passwd))) {
				uni.showToast({
					title: '密码错误',
					icon: "none"
				});
				return false;
			}
			uni.showLoading({
				title: '提交中...'
			})
			var data = {
				email: this.email,
				pwd: md5(this.passwd)
			}
			uni.request({
					url: 'http://120.79.19.253:10086/Login',
					data,
					success: (result) => {
						console.log("###################################这是登陆之后的数据", result.data);
						this.Status = result.data.status;
						if (this.Status === 1) {
							var token = result.data.token;
							uni.setStorage({
								key: 'token',
								data: token,
								success: function() {
									uni.showToast({
										title: '登陆成功',
										position: "center",
										image: "../../static/img/mysuccess.png"
									});
									uni.navigateTo({
										url:'../tabBar/user/user'
									});
								},
								fail: function() {
									uni.showToast({
										title: '用户数据写入失败，建议重启本应用',
										position: "center",
										image: "../../static/img/myfail.png"
									})
								}
							})
						} else {
							uni.showToast({
								title: '用户名或密码有误',
								position: "center",
								image: "../../static/img/myfail.png"
							});
						}
						this.text = 'request success';
					}
			})
		}
	}
}
